.cryo-wrapper {
  background-color: rgba(0, 0, 0, 0.3);
}

.cryo-default-style {
  background-color: rgba(255,255,255,0.9);
  color: #000;
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0 0 4px #666;
  max-width: 200px;
  text-align: center;
}

.cryo-default-style-2 {
  background-color: rgba(255,255,255,0.9);
  color: #000;
  padding: 20px;
  box-shadow: 0 0 4px rgb(48, 158, 209);
  max-width: 250px;
  text-align: center;
}
.cryo-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cryo-wrapper.fixed {
  position: fixed;
}

.cryo-freeze {
  pointer-events: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  -moz-user-select: none;
       user-select: none;
  position: relative;
}
